'use client';
import { useState } from 'react';
import HolographicForm from '../components/HolographicForm';
import StatusBadge from '../components/StatusBadge';
import CyberAlert from '../components/CyberAlert';
import styles from '../styles/cyber.module.css';

const technicalItems = [
  { id: 1, name: 'Motor Mode', requirement: 'No abnormal sounds or vibrations' },
  { id: 2, name: 'Engine Labor', requirement: 'Smooth operation within normal RPM range' },
  { id: 3, name: 'Suspension Status', requirement: 'Proper damping and no leaks' },
  // ... Continue with all 25 items
  { id: 25, name: 'Battery Status', requirement: 'Proper voltage and charge capacity' }
];

export default function TechnicalEval({ onNext, onBack, initialData }) {
  const [assessments, setAssessments] = useState(initialData || {});

  const handleAssessment = (id: number, status: string) => {
    setAssessments(prev => ({
      ...prev,
      [id]: { ...prev[id], status, date: new Date().toISOString() }
    }));
  };

  const handleSubmit = () => {
    if (Object.keys(assessments).length < technicalItems.length) {
      return alert('Please assess all technical items');
    }
    onNext({ technical: assessments });
  };

  const evaluations = Object.values(assessments).map((item: any) => ({
    status: item.status,
    duration: item.duration,
    adjusted: item.adjusted
  }));

  return (
    <HolographicForm title="Technical Evaluation" onBack={onBack}>
      <CyberAlert evaluations={evaluations} />
      
      <div className={styles.assessmentGrid}>
        {technicalItems.map(item => (
          <div key={item.id} className={styles.assessmentItem}>
            <h4>{item.name}</h4>
            <p>{item.requirement}</p>
            
            <div className={styles.statusSelector}>
              {['pass', 'medium', 'fail'].map(status => (
                <button
                  key={status}
                  type="button"
                  className={`${styles.statusButton} ${assessments[item.id]?.status === status ? styles.selected : ''}`}
                  onClick={() => handleAssessment(item.id, status)}
                >
                  <StatusBadge status={status} />
                  {status === 'pass' ? 'Pass' : status === 'medium' ? 'Medium' : 'Fail'}
                </button>
              ))}
            </div>

            {assessments[item.id]?.status === 'medium' && (
              <div className={styles.adjustmentField}>
                <label>
                  <input 
                    type="checkbox" 
                    checked={assessments[item.id]?.adjusted || false}
                    onChange={(e) => setAssessments(prev => ({
                      ...prev,
                      [item.id]: { ...prev[item.id], adjusted: e.target.checked }
                    }))}
                  />
                  Adjusted/Repaired
                </label>
              </div>
            )}

            {assessments[item.id]?.status === 'fail' && (
              <div className={styles.durationField}>
                <label>Problem Duration:</label>
                <select
                  value={assessments[item.id]?.duration || ''}
                  onChange={(e) => setAssessments(prev => ({
                    ...prev,
                    [item.id]: { ...prev[item.id], duration: e.target.value }
                  }))}
                >
                  <option value="">Select duration</option>
                  <option value="new">New problem</option>
                  <option value="30-days">Existing (30+ days)</option>
                </select>
              </div>
            )}
          </div>
        ))}
      </div>

      <div className={styles.formActions}>
        <button type="button" onClick={onBack} className={styles.cyberButtonOutline}>
          Back
        </button>
        <button type="button" onClick={handleSubmit} className={styles.cyberButton}>
          Continue to Problem Documentation
        </button>
      </div>
    </HolographicForm>
  );
}